<template>
  <div class="nav-list">
    <div class="animate__animated animate__jello">
      <!-- 动画 -->
      <div class="logo">Xgun</div>
    </div>
    <transition enter-active-class="animate__animated animate__bounceIn animate__faster">
      <div class="menu" v-show="nav_show">
        <!-- 动画 -->
        <Menu :active-name="1" :mode="mode">
          <MenuItem v-for="i,index in list" v-if="!i.is_sub" :key="index" :to="i.herf" :name="index">
          <div class="title">
            {{i.title}}
          </div>
          </MenuItem>
          <Submenu v-else :name='index'>
            <div class="title" slot="title">
              {{i.title}}
            </div>
            <MenuItem v-for="i_child,index_child in i.sub_item" :key="index_child" :to="i_child.herf"
              :name="index+'-'+index_child">
            <div class="child-title">
              {{i_child.title}}
            </div>
            </MenuItem>
          </Submenu>
        </Menu>
      </div>
    </transition>
    <div class="menu-btn" @click="show">
      <svg fill="currentColor" class="btn" viewBox="0 0 16 16">
        <path fill-rule="evenodd"
          d="M2.5 12a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z" />
      </svg>
    </div>
    <div class="mask" @click.stop="hiden" v-show="nav_show"></div>
  </div>
</template>

<script>
  import 'animate.css';
  import navList from '@/navList.json';
  export default {
    data() {
      return {
        list: [],
        mode: 'vertical',
        nav_show: false
      }
    },
    methods: {
      show() {
        this.nav_show = !this.nav_show
      },
      hiden() {
        this.nav_show = false;
      }
    },
    mounted() {
      console.log(navList)
      this.list = navList.navList
    }
  }
</script>

<style scoped>
  .nav-list {
    box-shadow: 1px 0px 10px 2px #ffffff;
    position: fixed;
    left: 0;
    top: 0;
    height: 100vh;
    width: 100vw;
    background-color: white;
    transition: all 1s ease;
    z-index: 10;
  }


  .nav-list-on {
    height: auto !important;
  }

  .mask {
    position: fixed;
    width: 100vw;
    height: 100vh;
    left: 0;
    top: 0;
    z-index: 2;
  }

  .logo {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;
    font-weight: bold;
  }


  .title {
    font-size: 0.1rem !important;
    margin-right: 10px;
  }

  >>>.ivu-menu-vertical .ivu-menu-item {
    padding-left: 0;
  }

  .child-title {
    padding-left: 15px;
    font-size: 8px;
  }

  .menu-btn {
    position: fixed;
    right: 10px;
    top: 10px;
    width: 48px;
    height: 48px;
    display: none;
  }

  .menu-btn .btn {
    width: 100%;
    height: 100%;
  }

  >>>.ivu-menu {
    width: auto !important;
  }

  >>>.ivu-menu-vertical .ivu-menu-submenu-title-icon {
    right: 0;
  }

  >>>.ivu-menu-vertical .ivu-menu-item,
  >>>.ivu-menu-vertical .ivu-menu-submenu-title {
    padding: 10px 15px;
    padding-right: 0;
  }
</style>

<style scoped="scoped">
  .fade-enter,
  .fade-leave-to {
  }

  .fade-enter-active,
  .fade-leave-active {
  }

  @keyframes list-pc-close {
    from {
      height: 100vw;
      width: 100vw;
    }

    to {
      width: 120px;
    }
  }

  @keyframes logo-pc-close {
    from {
      height: 100vh;
      font-size: 50px;
    }

    to {
      font-size: 10px;
      height: 120px;
    }
  }

  @keyframes menu-open {
    from {
      height: 0;
    }

    to {
      height: auto;
    }
  }

  @media screen and (min-width: 1200px) {
    .nav-list {
      animation: list-pc-close .5s ease-in 1s;
      animation-fill-mode: forwards;
    }

    .logo {
      animation: logo-pc-close .5s ease-in 1s;
      animation-fill-mode: forwards;
    }

    .menu {
      animation: menu-open .5s ease-in 1s;
      animation-fill-mode: forwards;
    }
  }

  @media screen and (max-width: 1200px) {
    .nav-list {
      top: 0;
      left: 0;
      width: 100vw;
      height: auto !important;
      line-height: 84px;
      flex-direction: row;
      z-index: 100;
      overflow: hidden;
      transition: all 0.2s ease-in;
    }

    .logo {
      height: 84px;
    }

    .menu-btn {
      display: block;
    }

    >>>.ivu-menu-vertical .ivu-menu-item,
    >>>.ivu-menu-vertical .ivu-menu-submenu-title {
      padding: 10px 60px;
    }
  }
</style>



