import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const route = new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: ()=>import("@/pages/instructions.vue")
    },
    {
      path: '/media',
      name: 'media',
      component: ()=>import("@/pages/media.vue")
    },
    {
      path: '/bullet',
      name: 'bullet',
      component: ()=>import("@/pages/bullet.vue")
    },
    {
      path: '/loadding',
      name: 'loadding',
      props: true,
      component: ()=>import("@/pages/loadding.vue")
    }
  ]
})

route.beforeEach((to, from, next)=>{
  console.log(to.query.LoadComplete)
  if( to.path != '/loadding' && from.path != '/loadding' && !to.query.LoadComplete ){
    // next('/loadding?path='+to.path)
    next('/loadding?path='+to.path)
  }else{
    next()
  }
})

export default route;



